import { Avatar, Box, Card, CardContent, Grid, Typography } from '@mui/material';
// import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
// import MoneyIcon from '@mui/icons-material/Money';

export const Person = (props) => (
  <Card
    sx={{ height: '100%' }}
    {...props}
  >
    <CardContent>
      <Grid
        container
        // spacing={3}
        sx={{ justifyContent: 'space-between'}}
      >
        <Grid item 
        >
          <Typography
            color="textSecondary"
            gutterBottom
            variant="overline"
          >
            {props.person.company.name || "BUDGET"}
          </Typography>
          </Grid>
        <Grid item>
          <Avatar
            sx={{
              backgroundColor: 'error.main',
              height: 44,
              width: 44
            }}
          >
          </Avatar>
        </Grid>
          <Grid item xs={12}>
          <Typography
            color="textPrimary"
            variant="h4"
          >
            {props.person.name || "$24k"}

          </Typography>
          </Grid>
        </Grid>
      <Box
        sx={{
          pt: 2,
          display: 'flex',
          alignItems: 'center'
        }}
      >
        {/* <ArrowDownwardIcon color="error" /> */}
        <Typography
          color="error"
          sx={{
            mr: 1
          }}
          variant="body2"
        >
          {props.person.email || "address..."}
        </Typography>
        <Typography
          color="textSecondary"
          variant="caption"
        >
          {props.person.phone || "address..."}
        </Typography>
      </Box>
    </CardContent>
  </Card>
);