import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  selectDevices,
  selectStatus,
  selectUsers,
  fetchUsersAsync,
} from './devicesSlice';
import styles from './Devices.module.css';
import { Device } from '../../components/Device';
import { Person } from '../../components/Person';
import { Box, Container, Grid, Button, Backdrop, CircularProgress } from '@mui/material';
import { addNotification } from '../notifications/notificationsSlice';
// import {fetchUsers} from './devicesAPI';

export default function Devices() {
  const devices = useSelector(selectDevices);
  const status = useSelector(selectStatus);
  const users = useSelector(selectUsers);
  const dispatch = useDispatch();
  const [incrementAmount, setIncrementAmount] = useState('2');

  const handleFetchUsers = () => {
    dispatch(fetchUsersAsync());
  }

  return (
    <Box sx={{ p: 3 }}>
      <div style={{ position: "absolute" }}>
        <Backdrop open={true} sx={{ color: '#444', zIndex: 1000, opacity: 0.5 }}
          onClick={() => console.log("onBackdropClose")}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </div>
      <h1>Users</h1>
      {/* <h3>Users count: {users.length}</h3> */}
      <Button onClick={() => handleFetchUsers()}>Fetch users</Button>
      <Grid
        container
        spacing={3}
      >
        {users && users.map((user) => (
          <Grid key={user.id}
            item
            lg={6}
            sm={6}
            xl={3}
            xs={12}
          >
            <Person person={user} />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
