import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  addNotification,
  dismissNotification,
  clearNofitications,
  toggleShowNotifications,
  selectShowNotifications,
  selectActiveNotifications,
} from './notificationsSlice';
import { Snackbar, Alert } from '@mui/material';
import styles from './Notifications.module.css';

export function Notifications() {
  const notifications = useSelector(selectActiveNotifications);
  const showNotifications = useSelector(selectShowNotifications);
  const dispatch = useDispatch();

  // const [open, setOpen] = React.useState(showNotifications);

  const handleClose = (event, reason, notificationId) => {
    if (reason === 'clickaway') {
      return;
    }
    dispatch(dismissNotification(notificationId));
  };

  const handleDismissNotification = (notificationId) => {
    dispatch(dismissNotification(notificationId));
  }

  const handleToggleShowNotifications = () => {
    dispatch(toggleShowNotifications());
  }

  const handleAddNotification = () => {
    const action = {
      notificationType: "normal",
      message: "Successfully added new notification.",
      icon: "",
    };
    dispatch(addNotification(action));
  }

  const notification = notifications[0];
  return (
    <div>
      
      {/* <Snackbar
        open={showNotifications}
        autoHideDuration={6000}
        onClose={handleClose}
        message="Note archived"
      /> */}

      {notification &&
        <Snackbar
          anchorOrigin={ {vertical: 'top', horizontal: 'right' }}
          open={showNotifications}
          autoHideDuration={showNotifications && notification.autoDismiss ? 3000 : null}
          onClose={(event, reason) => {
            if (reason === 'clickaway') return;
            handleDismissNotification(notification.id)
          }}
        >
          <Alert onClose={() => handleDismissNotification(notification.id)} severity="success" sx={{ width: '100%' }}>
            {notification.message}
          </Alert>
        </Snackbar>
      }
    </div>
  );
}
