import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { useDispatch } from 'react-redux';
import { addNotification } from '../notifications/notificationsSlice';

const initialState = {
  value: 0,
  currentLayout: "main",
  status: 'idle',
  currentPage: "home",
  theme: "light",
};

export const layoutSlice = createSlice({
  name: 'layout',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    setCurrentPage: (state, action) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.currentPage = action.payload;
    },
    setTheme: (state, action) => {
      state.theme = action.payload
      // const dispatch = useDispatch();

      // useDispatch(addNotification({
      //   notificationType: "normal",
      //   message: action.payload === "light" ? "Switched to Light mode!" : "Switched to Dark mode!",
      //   icon: "",
      //   dismissed: false,
      //   autoDismiss: true,
      //   autoDismissAfter: 3000,
      // }))
    }
  },
});

export const { setCurrentPage, setTheme } = layoutSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.layout.value)`
export const selectLayout = (state) => {
  return state.layout.currentLayout
};
export const selectCurrentPage = (state) => {
  return state.layout.currentPage
};
export const selectTheme = (state) => {
  return state.layout.theme
};

export default layoutSlice.reducer;
