import React from 'react'
import { Devices } from '../../features/devices/Devices'
import { Device } from '../../components/Device'
import { Box, Container, Grid } from '@mui/material';


export default function Home() {
  return (
    // <Container maxWidth={false}>
    <Box sx={{p: 3}}>
      <h1>Home</h1>
      <Grid
        container
        spacing={3}
      >
        <Grid
          item
          lg={3}
          sm={6}
          xl={3}
          xs={12}
        >
          <Device />
        </Grid>
        <Grid
          item
          xl={3}
          lg={3}
          sm={6}
          xs={12}
        >
          <Device />
        </Grid>
        <Grid
          item
          xl={3}
          lg={3}
          sm={6}
          xs={12}
        >
          <Device />
        </Grid>
        <Grid
          item
          xl={3}
          lg={3}
          sm={6}
          xs={12}
        >
          <Device sx={{ height: '100%' }} />
        </Grid>
        <Grid
          item
          lg={8}
          md={12}
          xl={9}
          xs={12}
        >
          <Device />
        </Grid>
        <Grid
          item
          lg={4}
          md={6}
          xl={3}
          xs={12}
        >
          <Device sx={{ height: '100%' }} />
        </Grid>
        <Grid
          item
          lg={4}
          md={6}
          xl={3}
          xs={12}
        >
          <Device sx={{ height: '100%' }} />
        </Grid>
        <Grid
          item
          lg={8}
          md={12}
          xl={9}
          xs={12}
        >
          <Device />
        </Grid>
      </Grid>
    {/* </Container> */}
    </Box>
  )
}
