import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchNotifications } from './notificationsAPI';
import { v4 as uuidv4 } from 'uuid';


const initialState = {
  value: 0,
  notifications: [{
    id: uuidv4(),
    notificationType: "normal",
    message: "A notification",
    icon: "",
    dismissed: false,
    autoDismiss: false,
  }],
  showNotifications: true,
  status: 'idle',
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const incrementAsync = createAsyncThunk(
  'notifications/fetchNotifications',
  async (amount) => {
    const response = await fetchNotifications(amount);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const notificationsSlice = createSlice({
  name: 'notifications',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    addNotification: (state, action) => {
      const id = uuidv4();
      state.value += 1;
      state.notifications = [
        ...state.notifications, {
          id: id,
          notificationType: action.payload.notificationType,
          message: action.payload.message,
          icon: action.payload.icon,
          dismissed: false,
          autoDismiss: action.autoDismiss || true,
          autoDismissAfter: action.payload.autoDismissAfter || 3000,
          createdAt: Date.now(),
        }
      ];
    },
    dismissNotification: (state, action) => {
      const nots = state.notifications;
      var notification = nots.find(x => x.id === action.payload);
      if(!nots) return;

      notification.dismissed = true;

      state.notifications = [...nots];
    },
    removeNotification: (state, action) => {
      var newStates = state.notifications.filter(notification => notification.id !== action.payload);

      state.notifications = [
        ...newStates
      ];
    },
    clearNotifications: (state) => {
      state.notifications = {};
    },
    toggleShowNotifications: (state) => {
      state.showNotifications = !state.showNotifications;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(incrementAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(incrementAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.value += action.payload;
      });
  },
});

export const { addNotification, dismissNotification, removeNotification, clearNofitications, toggleShowNotifications } = notificationsSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.notifications.value)`
export const selectNotifications = (state) => state.notifications.notifications;
export const selectActiveNotifications = (state) => state.notifications.notifications.filter(x => !x.dismissed)
export const selectShowNotifications = (state) => state.notifications.showNotifications;


export default notificationsSlice.reducer;
